export default function (state={},action) {
    switch (action.type) {
        case 'GET_BOOKS':
            return {...state,list:action.payload};

        case 'GET_BOOK':
            return {...state,book:action.payload};

        case 'ADD_BOOK':
            return {...state,newbook:action.payload};

        case 'UPDATE_BOOK':
            return {...state,updateBook:action.payload.success,book:action.payload.doc};

        case 'DELETE_BOOK':
            return {...state,postDeleted:action.payload};

        case 'CLEAR_BOOK':
            return {...state, book:action.payload.book, updateBook:action.payload.updateBook, postDeleted:action.payload.postDeleted};

        case 'CLEAR_NEW_BOOK':
            return {...state,newbook:action.payload};
            
        case 'GET_BOOK_W_R':
            return {
                ...state,
                book:action.payload.book,
                reviewer:action.payload.reviewer
            };

        case 'CLEAR_BOOK_W_R':
            return {
                ...state,
                book:action.payload.book,
                reviewer:action.payload.reviewer
            };

        default:
            return state;
    }
}


